<template>
  <div>
    <div ref="term"></div>
    <v-btn color="error" @click="clear">清空</v-btn>
    <v-layout>
      <v-select v-model="command" :items="commandList" label="常用命令" autocomplete></v-select>
      <v-btn color="cyan" @click="action" :disabled="!command">执行</v-btn>
      <v-dialog v-model="dialog" persistent max-width="500px">
        <v-btn color="primary" dark slot="activator">添加常用命令</v-btn>
        <v-card>
          <v-card-title>
            <span class="headline">添加常用命令</span>
          </v-card-title>
          <v-card-text>
            <v-text-field label="名称" v-model.trim="newcommand.text" required></v-text-field>
            <v-text-field label="命令" v-model.trim="newcommand.value" required multi-line></v-text-field>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" flat @click.native="dialog=false">关闭</v-btn>
            <v-btn color="blue darken-1" flat @click.native="add()" :disabled="!newcommand.value">确定</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-layout>
  </div>
</template>
<script>
import { fetchInfo } from '../../api/ssh'
import { Terminal } from 'xterm'
import io from 'socket.io-client'
import 'xterm/dist/xterm.css'

export default {
  data() {
    return {
      dialog: false,
      newcommand: {
        text: '',
        value: ''
      },
      command: '',
      commandList: [],
      term: null,
      socket: null
    }
  },
  created() {
    var commandList = localStorage.getItem('commandList')
    if (commandList) {
      this.commandList = JSON.parse(commandList)
    }
  },
  async mounted() {
    // console.log(getScript, fetchInfo)
    var data = await fetchInfo(this.$route.params.id)
    this.start(data);
  },
  beforeDestroy() {
    if (this.socket) {
      this.socket.close();
    }
    if (this.term) {
      this.term.destroy()
    }
  },
  methods: {
    start(data) {
      var ele = this.$refs.term
      var term = this.term = new Terminal({
        cursorBlink: true,  // Do not blink the terminal's cursor
        cols: 120
      });
      var socket = this.socket = io.connect('http://localhost:3000');
      socket.on('connect', e => {
        socket.emit('login', data)
        socket.once('success', () => {
          term.writeln('登录成功-_-')
        })
      })
      socket.on('message', d => {
        term.write(d);
      })
      socket.once('failed', d => {
        term.writeln('登录失败-**-')
        term.write(d);
      })
      term.open(ele, true);
      term.writeln('欢迎使用!!!');
      term.writeln('正在登录服务器...');
      /* term.on('paste', data => {
        socket.send(data)
      })
      term.on('key', (key, ev) => {
        socket.send(key)
      }); */
      term.on('data', data => {
        socket.send(data)
      })
    },
    clear() {
      if (this.term) {
        this.term.reset();
        this.term.focus();
      }
    },
    add() {
      this.dialog = false;
      this.commandList.push(this.newcommand);
      this.newcommand = {
        text: '',
        value: ''
      };
      localStorage.setItem('commandList', JSON.stringify(this.commandList))
    },
    action() {
      if (this.socket) {
        this.socket.send(this.command + '\n')
      }
    }
  }
}
</script>


